body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-content {
  margin-top: 25px;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.sidebarToggle {
}
.sidenav-content {
  background-color: #384158;
  width: 360px;
  position: fixed;
  top: 25px;
  bottom: 0px;
  z-index: 1;
}

.sidenav-wrapper [data-icon="times"] {
  position: fixed;
  left: 363px;
  top: 29px;
  color: white;
  z-index: 2;
  cursor: pointer;
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
  opacity: 0.5;
}
.sidenav-wrapper [data-icon="bars"] {
  position: fixed;
  left: 5px;
  top: 29px;
  color: white;
  z-index: 2;
  cursor: pointer;
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
  opacity: 0.5;
}
.sidenav-wrapper [data-icon]:hover {
  opacity: 1;
}
.sidenav-primary-toolbar {
  background-color: #263950;
  height: 25px;
  color: rgba(87, 185, 255, 0.9098039215686274);
  display: flex;
  align-items: center;
}
.sidenav-primary-toolbar svg {
  margin-left: 8px;
  cursor: pointer;
}
.sidenav-primary-toolbar svg:hover {
  color: aqua;
}
.sidenav-folder-wrapper {
  display: flex;
  flex-direction: column;
  padding-left: 20px;
  padding-right: 10px;
  padding-top: 4px;
  color: #acb4b9;
}
.sidenav-folder-wrapper .folder-name {
  padding-left: 6px;
  flex: 1 1;
  justify-content: flex-start;
  display: flex;
  align-items: center;
}
.sidenav-folder-wrapper .folder-name [data-icon="pen"] {
  display: none;
}
.sidenav-folder-wrapper .folder-name:hover [data-icon="pen"],
.sidenav-folder-wrapper .folder-name [data-icon="pen"]:hover {
  display: block;
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
  padding-left: 10px;
}
.sidenav-folder-wrapper .folder-name input {
  background-color: #384158;
  border: none;
  color: #54e64a;
  outline: none;
}
.sidenav-folder-wrapper .folder-header {
  display: flex;
  align-items: center;
}

.sidenav-folder-wrapper .folder-header:hover {
  cursor: pointer;
  color: white;
}

.folder-header:hover .folder-options-wrapper {
  display: flex;
  align-items: center;
}

.folder-options-wrapper,
.test-case-options-wrapper {
  display: none;
}
.folder-options-wrapper > svg,
.test-case-options-wrapper > svg {
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
}
.folder-options,
.test-case-options {
  display: flex;
  position: absolute;
  background-color: #171d67;
  -webkit-transform: translateX(-60%);
          transform: translateX(-60%);
  border-radius: 20%;
  display: none;
  z-index: 2;
  padding: 0px 2px;
}
.folder-options:hover,
.test-case-options:hover,
[data-icon="ellipsis-h"]:hover ~ .test-case-options,
[data-icon="ellipsis-h"]:hover ~ .folder-options {
  display: flex;
}

.folder-options [data-icon="trash"],
.test-case-options [data-icon="trash"] {
  color: red;
}
.folder-options [data-icon="play"],
.test-case-options [data-icon="play"] {
  color: greenyellow;
}
.folder-options [data-icon="plus"] {
  color: silver;
}
.folder-options svg,
.test-case-options svg {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  padding: 0px 2px;
}

.folder-options svg:hover,
.test-case-options svg:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
.folder-content {
  padding-left: 16px;
}
.test-case {
  display: flex;
  align-items: center;
}
.test-case-name {
  flex: 1 1;
  display: flex;
  padding-left: 5px;
}
.test-case-name.running{
  color: #10f121;
}
.test-case [data-icon="vial"] {
  color: #b31616;
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
}
.test-case:hover {
  cursor: pointer;
}
.test-case:hover [data-icon="ellipsis-h"] {
  color: white;
}
.test-case:hover .test-case-name {
  color: white;
}
.test-case:hover .test-case-options-wrapper {
  display: flex;
  align-items: center;
}

.loading {
  position: absolute;
  left: 32px;
  width: 20px;
  height: 20px;
  border-radius: 10px;
  border: 2px solid #10f121;
  border-top-color: rgba(0, 0, 0, 0.3);
  box-sizing: border-box;
  animation: loading 1.2s linear infinite;
  -webkit-animation: loading 1.2s linear infinite;
}
@keyframes loading {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@-webkit-keyframes loading {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}

.top-bar-wrapper {
  position: fixed;
  top: 0px;
  right: 0px;
  left: 0px;
  z-index: 1;
  height: 25px;
  background-color: #646b73;
  display: flex;
  align-items: center;
}
.top-bar-wrapper .menu {
  flex: 2 1;
}

.top-bar-wrapper .current-selection {
  flex: 4 1;
  display: flex;
  align-items: center;
  color: #afefda;
}

.top-bar-wrapper .tools {
  flex: 0.5 1;
}

.top-bar-wrapper .user-info {
  flex: 1 1;
  display: flex;
  flex-direction: row-reverse;
  color: white;
  padding-right: 10px;
}

.current-selection [data-icon="folder"] {
  color: gold;
  padding-right: 10px;
}

.current-selection [data-icon="vial"] {
  color: #b31616;
  padding-right: 10px;
}
.tools-options-container {
  display: flex;
  flex: 1 1;
  background-color: #2b333e;
}
.tools-options-container [data-icon] {
  padding: 0px 4px;
}
.tools-options-container [data-icon]:hover {
  cursor: pointer;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

[data-icon="plus"] {
  color: silver;
}
[data-icon="play"] {
  color: greenyellow;
}
[data-icon="trash"] {
  color: red;
}


.test-case-compact-wrapper{
    display: flex;
    flex-direction: column;
    background-color: #0f3a4a;
    align-self: stretch;    
    margin-bottom: 2px;
}
.test-case-compact-header{
    padding-left: 25px;
    display: flex;
    height: 40px;
    background-color: #082935;
}
.test-case-compact-steps{
    min-height: 60px;
}

.test-case-compact-header .test-case-name{
    cursor: pointer;
}
.test-case-wrapper {
  flex: 1 1;
  align-self: stretch;
  display: flex;
  flex-direction: column;
  background-color: #0f3a4a;
}
.test-case-wrapper .test-case-header {
  height: 40px;
  background-color: #082935;
  display: flex;
  justify-content: center;
  align-items: center;
}

.test-case-wrapper .test-case-header [data-icon="pen"] {
  display: none;
}
.test-case-wrapper .test-case-header:hover [data-icon="pen"],
.test-case-wrapper .test-case-header [data-icon="pen"]:hover {
  display: block;
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
  padding-left: 10px;
  cursor: pointer;
}
.test-case-wrapper .test-case-header input {
  font-size: calc(10px + 2vmin);
  background-color: #082935;
  border: none;
  color: #54e64a;
  outline: none;
}

.step-wrapper {
  flex: 1 1;
  background-color: #0f3a4a;
  min-height: 105px;
  display: flex;
  flex-direction: column;
  font-size: calc(5px + 2vmin);
}
.step-connector {
  height: 10px;
  width: 5px;
}
.step-header {
  background-color: #03646d;
  display: flex;
  padding-left: 20px;
  height: 35px;
  font-size: calc(8px + 2vmin);
  align-items: center;
}
.step-type {
  padding-left: 20px;
  display: flex;
  margin-top: 10px;
}
.step-type-options {
  display: flex;
  flex-direction: column;
}
.step-type-options select {
  background-color: #0f3a4a;
  color: white;
  height: 20px;
}

.step-data {
  display: flex;
  margin-left: 20px;
  margin-top: 10px;
  margin-bottom: 5px;
}

.step-data input {
  padding: 5px 2px;
  width: 100%;
  background-color: #0f3a4a;
  color: white;
  border: none;
  border: 1px solid #2f6173;
  outline: none;
}

.step-data textarea {
  padding: 5px 2px;
  width: 100%;
  background-color: #0f3a4a;
  color: white;
  border: none;
  border: 1px solid #2f6173;
  outline: none;
}

.step-wrapper .step-header [data-icon="pen"] {
  display: none;
}
.step-wrapper .step-header:hover [data-icon="pen"],
.step-wrapper .step-header [data-icon="pen"]:hover {
  display: block;
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
  padding-left: 10px;
  cursor: pointer;
}
.step-wrapper .step-header input {
  font-size: calc(8px + 2vmin);
  background-color: #03646d;
  border: none;
  color: #54e64a;
  outline: none;
}
.step-container {
  display: flex;
}
.step-data-wrapper {
  display: flex;
  flex-direction: column;
  flex: 1 1;
}
.step-reponse-wrapper {
  display: flex;
  flex: 1 1;
  color: #bdbdef;
}
.response-row-wrapper {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  margin-left: 40px;
  margin-top: 10px;
}
.response-row {
  display: flex;
}
.response-row-image {
  display: flex;
  flex-direction: column;
}
.response-row-image .label {
  display: flex;
}
.response-row-image img {
  height: 300px;
  border: 1px solid;
}
.response-row-image .image-wrapper {
  display: flex;
}
.response-row .label {
  display: flex;
  flex: 1 1;
}
.response-row .data {
  display: flex;
  flex: 4 1;
}

.response-row .data.Pass {
  font-weight: bold;
  color: #54e64a;
}

.response-row .data.Fail {
  font-weight: bold;
  color: red;
}

